<template>
  <div>
    <el-tabs class="full-width"
             v-model="activeName"
    >
      <el-tab-pane
        label="Thuộc tính phiên bản"
        name="variant"
      >
        <div class="row">
          <div class="col-lg-4 col-md-6 col-sm-6 col-xs-12 card-attribute" v-for="variantAttribute of variantAttributes">
            <div class="card">
              <div class="card-header">
                <h6 class="title">{{variantAttribute.name}}</h6>
              </div>
              <hr>
              <div class="card-body card-body-attribute">
                <el-table
                  :data="variantAttribute.child"
                  style="width: 100%">
                  <el-table-column
                    prop="name"
                    label="Tiêu đề"
                    width="280">
                  </el-table-column>
                  <el-table-column
                    :min-width="120"
                    fixed="right"
                    class-name="td-actions"
                    label="Hành động"
                  >
                    <template slot-scope="props">
                      <p-button
                        type="primary" size="sm" icon @click="showModal(props.row, 'variant', 'edit')"
                      >
                        <i class="nc-icon nc-ruler-pencil"></i>
                      </p-button>
                      <p-button
                        type="danger" size="sm" icon @click="remove(props.$index, props.row, 'variant')"
                      >
                        <i class="nc-icon nc-simple-remove"></i>
                      </p-button>
                    </template>
                  </el-table-column>
                </el-table>
              </div>
              <hr>
              <div class="card-body">
                <el-button type="info" plain size="small" class="full-width" @click="showModal(variantAttribute, 'variant', 'add')">Thêm mới</el-button>
              </div>
            </div>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane
        label="Thuộc tính sản phẩm"
        name="product"
      >
        <div class="row">
          <div class="col-lg-4 col-md-6 col-sm-6 col-xs-12 card-attribute" v-for="productAttribute of productAttributes">
            <div class="card">
              <div class="card-header">
                <h6 class="title">{{productAttribute.name}}</h6>
              </div>
              <hr>
              <div class="card-body card-body-attribute">
                <el-table
                  :data="productAttribute.child"
                  style="width: 100%">
                  <el-table-column
                    prop="name"
                    label="Tiêu đề"
                    width="280">
                  </el-table-column>
                  <el-table-column
                    :min-width="120"
                    fixed="right"
                    class-name="td-actions"
                    label="Hành động"
                  >
                    <template slot-scope="props">
                      <p-button
                        type="primary" size="sm" icon @click="showModal(props.row, 'product', 'edit')"
                      >
                        <i class="nc-icon nc-ruler-pencil"></i>
                      </p-button>
                      <p-button
                        type="danger" size="sm" icon @click="remove(props.$index, props.row, 'product')"
                      >
                        <i class="nc-icon nc-simple-remove"></i>
                      </p-button>
                    </template>
                  </el-table-column>
                </el-table>
              </div>
              <hr>
              <div class="card-body">
                <el-button type="info" plain size="small" class="full-width" @click="showModal(productAttribute, 'product', 'add')">Thêm mới</el-button>
              </div>
            </div>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
    <el-dialog title="Thuộc tính" :visible.sync="dialogAddVisible" width="30%">
      <el-form :model="formCreateAttribute">
        <el-form-item label="Tiêu đề">
          <el-input
            v-model="formCreateAttribute.name"
            :class="errors.has('name')?'border-danger':''"
            v-validate="'required'"
            data-vv-name="name"
            data-vv-as="Tiêu đề"
          ></el-input>
          <span class="text-danger" v-if="errors.has('name')">{{ errors.first('name') }}</span>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogAddVisible = false">Đóng</el-button>
          <el-button type="submit" class="btn-primary" @click="addAttribute">{{ this.action == 'add' ? 'Thêm thuộc tính' : 'Cập nhật'}}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import {Table, TableColumn, Select, Option, Button, Tabs, TabPane, Input, MessageBox, Message, Dialog, Form, FormItem} from 'element-ui'
  import { mapState } from 'vuex'

  export default {
    components: {
      ElTable: Table,
      ElTableColumn: TableColumn,
      ElSelect: Select,
      ElOption: Option,
      ElButton: Button,
      ElTabs: Tabs,
      ElTabPane: TabPane,
      ElInput: Input,
      ElDialog: Dialog,
      ElForm: Form,
      ElFormItem: FormItem,
      Message,
      MessageBox
    },
    data() {
      return {
        activeName: 'variant',
        selectAttribute: [],
        selectType: '',
        action: '',
        formCreateAttribute: {
          name: ''
        },
        dialogAddVisible: false
      }
    },
    computed: mapState(['variantAttributes', 'productAttributes']),
    mounted() {
      this.$store.dispatch('fetchProductAttributes');
      this.$store.dispatch('setPageTitle', 'Danh sách thuộc tính');
    },
    methods: {
      showModal(attribute, type, action){
        this.selectAttribute = attribute;
        this.selectType = type;
        this.action = action;
        if (action == 'edit'){
          this.formCreateAttribute.name = attribute.name;
        } else this.formCreateAttribute.name = '';
        this.dialogAddVisible = true;
      },
      addAttribute(){
        let self = this;
        this.$validator.validateAll().then(function (result) {
          if (result) {
            let data = {};
            data.name = self.formCreateAttribute.name;
            data.type = self.selectType;
            if (self.action == 'add') {
              data.parent_id = self.selectAttribute.id;
              self.$store.dispatch('storeAttribute', data).then(res => {
                Message({
                  message: 'Tạo mới thành công',
                  type: 'success'
                });
              }, res => {
                Message({
                  message: res.message,
                  type: 'danger'
                });
              });
            }
            else {
              data.id = self.selectAttribute.id;
              data.parent_id = self.selectAttribute.parent_id;
              self.$store.dispatch('updateAttribute', data).then(res => {
                Message({
                  message: 'Cập nhật thành công',
                  type: 'success'
                });
              }, res => {
                Message({
                  message: res.message,
                  type: 'danger'
                });
              });
            }
            self.dialogAddVisible = false;
            self.selectAttribute = [];
            self.formCreateAttribute = [];
            self.selectType = '';
            self.action = '';
          }
        })
      },
      remove(index, row, type){
        MessageBox.confirm('Bạn có chắc chắn xóa không?', 'Warning', {
          confirmButtonText: 'Đồng ý',
          cancelButtonText: 'Hủy bỏ',
          type: 'warning',
          center: true
        }).then(() => {
          this.$store.dispatch('removeAttribute', {
            id: row.id,
            type: type
          }).then(res => {
            Message({
              message: 'Đã xóa thuộc tính',
              type: 'success'
            });
          }, res => {
            Message({
              message: "Có lỗi xảy ra",
              type: 'danger'
            });
          });
        }).catch(() => {
          Message({
            type: 'info',
            message: 'Hủy bỏ'
          });
        })
      }
    },
  }
</script>

<style scoped>
  .card-body-attribute{
    height: 300px;
    overflow-y: auto;
  }
</style>
